<p><span class="rvts49">召唤学派（</span><span class="rvts61">Summoning</span><span class="rvts49">）：</span>
<br><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber352.jpg"><span class="rvts18"></span></p>
<p><span class="rvts21">召唤学派的技能一共有</span><span class="rvts20">27</span><span class="rvts21">个（其中包含</span><span class="rvts20">4</span><span class="rvts21">个任务召唤技能）。召唤学派通过升级该学派的技能点来提升技能伤害和召唤品的生命，攻击，防御和抵抗。</span>
<br><span class="rvts21">召唤学派主要利用化身，召唤物和图腾来进行辅助战斗。利用地形特征，为召唤品提供更好的增益加成将大大提高该派系的作战能力。</span>
<br><span class="rvts48"></span>
<br><span class="rvts29">召唤学派技能书可以在以下</span><span class="rvts33">NPC</span><span class="rvts29">中买到：</span>
<br><span class="rvts20">1. </span><span class="rvts21">耐博拉（</span><span class="rvts20">Nebora</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">欢乐堡</span>
<br><span class="rvts20">2. </span><span class="rvts21">科本（</span><span class="rvts20">Kerban</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿玛迪亚避难所</span>
<br><span class="rvts20">3. </span><span class="rvts21">商人布理（</span><span class="rvts20">Trader Bree</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">浮木城</span>
<br><span class="rvts20">4. </span><span class="rvts21">黑环掠夺者（</span><span class="rvts20">Black Ring Reaver</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">费勒斯神庙南部，无名岛</span>
<br><span class="rvts20">5. </span><span class="rvts21">武僧蜥蜴（</span><span class="rvts20">Lizard Monk</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">泰森地留斯神庙，无名岛</span>
<br><span class="rvts20">6. </span><span class="rvts21">商人阿若瓦伊（</span><span class="rvts20">Trader Avarae</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
