<br><span class="rvts29">特殊召唤技能：</span>
<br><span class="rvts21">特殊召唤技能存在于特殊的召唤任务中，玩家完成该任务便会取得该召唤技能（有些任务一旦失败的话，会再也无法学到该技能）。</span>
<br><span class="rvts21">被召唤的宝宝根据类型不同，拥有独自的技能系统。所有的特殊召唤技能无需玩家的召唤系技能等级。</span>
<br><span class="rvts63">1. </span><span class="rvts41">召唤猫同伴（</span><span class="rvts63">Summon Cat Familiar</span><span class="rvts41">）</span><span class="rvts63">:</span>
